import React from "react"
import { graphql } from "gatsby"
import ReactMarkdown from "react-markdown/with-html"
import Layout from "../components/layout"
import CodeBlock from "../components/CodeBlock"
import SEO from "../components/SEO"

export default function HandoutTemplate({
  data, // this prop will be injected by the GraphQL query below.
  pageContext,
}) {
  const { title } = pageContext
  const { markdownRemark } = data // data.markdownRemark holds markdown data
  const { rawMarkdownBody } = markdownRemark

  return (
    <Layout>
      <SEO title={title} />
      <div className="handout-container">
        <div className="handout">
          {/* <h1>{frontmatter.title}</h1> */}
          {/* <h2>{frontmatter.date}</h2> */}
          <ReactMarkdown
            source={rawMarkdownBody}
            renderers={{
              code: CodeBlock,
              inlineCode: ({ value }) => (
                <code
                  style={{
                    color: "#fff",
                    fontSize: "1.2rem",
                    backgroundColor: "#3949ab",
                    padding: "1px 5px",
                    borderRadius: "5px",
                  }}
                >
                  {value}
                </code>
              ),
            }}
            escapeHtml={false}
          />
        </div>
      </div>
    </Layout>
  )
}
export const pageQuery = graphql`
  query HandoutQuery($fileAbsolutePath: String!) {
    markdownRemark(fileAbsolutePath: { eq: $fileAbsolutePath }) {
      rawMarkdownBody
    }
  }
`
